    Techdesign
</template>

<script>
  import Techdesign from '@/components/Techdesign'

  export default {
    components: {
      Techdesign
    }
  }
</script>

<style lang="sass" scoped>
  #Dos

    blockquote
      color: #efefef
      text-align: left
      line-height: 1.3
      font-weight: 200
      font-size: calc(1.25rem + (3 - 2) * (100vw - 32rem)/ (76.8 - 32))
      margin: calc(2.5rem + (6 - 4) * (100vw - 32rem)/ (76.8 - 32)) 0
      opacity: 0

      &.in-viewport
