        // &:last-child
        //   margin-bottom: calc(1.5rem + (6 - 4) * (100vw - 32rem)/ (76.8 - 32))
        &.in-viewport
          animation: 1.25s fadeInUp2 550ms both

      > strong
        color: #fff
        font-weight: 600
</style>






